import React, { useEffect, useState } from 'react';
import { Button, Form, Input, message, Modal, Switch, Table } from 'antd';
import type { ColumnsType } from 'antd/es/table';
import { DeleteOutlined, ExclamationCircleOutlined, EditOutlined } from '@ant-design/icons';
import { connect } from 'react-redux';
import { Dispatch, bindActionCreators } from 'redux';
import * as BlogActions from '@/redux/actionCreator';
import MyPagination from '@/components/pagination';
import dayjs from 'dayjs';
import jwtDecode from 'jwt-decode';
import TextArea from 'antd/es/input/TextArea';
import { handleNotAdd, handleNotChangeStatus, handleNotDelete, handleNotUpdate } from '@/utils/prompt';
const { confirm } = Modal;
const { Search } = Input;
interface DataType {
  author: string;
  key: React.Key;
  _id: string;
  checked: boolean;
  content: string;
  createTime: string;
  updateTime: string;
}
interface ApothegmData {
  checked: any;
  avatar: any;
  _id: string;
  totalCount: number;
  page: number;
  pageSize: number;
  data: DataType[];
}
const ApothegmList = (props: any) => {
  const token = jwtDecode(localStorage.getItem('token') as string) as object | any;
  const role_type = token[0].role[0].role_type

  const columns: ColumnsType<DataType> = [
    {
      title: '‰ΩúËÄÖ',
      dataIndex: 'author',
      render: (_, record) => {
        return <p className='introduction' style={{ width: '6rem' }}>{record.author}</p>;
      },
    },
    {
      title: 'ÂÜÖÂÆπ',
      dataIndex: 'content',
      render: (_, record) => {
        return <p className='introduction' style={{ width: '20rem' }}>{record.content}</p>;
      },
    },
    {
      title: 'Â±ïÁ§∫Áä∂ÊÄÅ',
      dataIndex: 'checked',
      render: (_, record: any) => {
        return (
          <Switch
            checkedChildren={'‰∏äÁ∫ø'}
            unCheckedChildren={'‰∏ãÁ∫ø'}
            checked={record.checked}
            onChange={checked => role_type ? handleNotChangeStatus() : onChangeStatus(checked, record)}
          />
        );
      },
    },
    {
      title: 'ÂàõÂª∫Êó∂Èó¥',
      dataIndex: 'createTime',
      render: time => {
        return dayjs(time * 1000).format('YYYY-MM-DD HH:mm:ss');
      },
    },
    {
      title: 'Êõ¥Êñ∞Êó∂Èó¥',
      dataIndex: 'updateTime',
      render: time => {
        return time === 0 ? 0 : dayjs(time * 1000).format('YYYY-MM-DD HH:mm:ss');
      },
    },
    {
      title: 'Êìç‰Ωú',
      key: 'action',
      className: 'handle_btn',
      render: item => {
        return (
          <div>
            <Button
              type="primary"
              danger
              shape="circle"
              icon={<DeleteOutlined />}
              onClick={() => {
                ApothegmDelete(item);
              }}
              style={{ marginRight: '5px' }}
            />
            <Button
              type="primary"
              ghost
              shape="circle"
              icon={<EditOutlined />}
              onClick={() => {
                ApothegmUpdate(item);
              }}
              style={{ marginRight: '5px' }}
            />
          </div>
        );
      },
    },
  ];
  // Ë°®ÂçïÊï∞ÊçÆ
  const [form] = Form.useForm();
  // Êõ¥Êñ∞Ë°®Âçï
  const [updateForm] = Form.useForm();
  // Ë≠¶Âè•ÂàóË°®
  const [list, setList] = useState<DataType[]>([]);
  // ÂàÜÈ°µÊÄªÊï∞
  const [total, setTotal] = useState(0);
  // ÂΩìÂâçÁ¨¨Âá†È°µ
  const [currentPage, setCurrentPage] = useState(1);
  // ÊØèÈ°µÊòæÁ§∫Êù°Êï∞
  const [pageSize, setPageSize] = useState(10);
  // Á™óÂè£
  const [isModalOpen, setIsModalOpen] = useState(false);
  // Êõ¥Êñ∞Á™óÂè£
  const [isModalUpdateOpen, setIsModalUpdateOpen] = useState(false);
  // ‰øùÂ≠òÂΩìÂâçÊõ¥Êñ∞ÁöÑÊï∞ÊçÆ
  const [editData, setEditData] = useState({});
  // Ëé∑ÂèñË≠¶Âè•ÂàóË°®Êï∞ÊçÆ
  useEffect(() => {
    props.BlogActions.asyncApothegmListAction(currentPage, pageSize, '').then((res: ApothegmData) => {
      // Ëé∑ÂèñË≠¶Âè•
      let { data, totalCount, page, pageSize } = res.data as unknown as ApothegmData;
      setList(data);
      setTotal(totalCount);
      setCurrentPage(page);
      setPageSize(pageSize);
    });
  }, [currentPage, pageSize, props.BlogActions]);

  // Êñ∞Â¢ûË≠¶Âè•
  const showModal = () => {
    setIsModalOpen(true);
  };
  // ÁÇπÂáªÁ°ÆÂÆöÊåâÈíÆ
  const handleConfirm = async () => {
    if (role_type) {
      return handleNotAdd()
    }
    // Ê†°È™åformÂÄº Ê†°È™åÈÄöËøáÂêéËé∑ÂèñÂÄº
    await form.validateFields();
    // Ëé∑ÂèñË°®ÂçïÂÄº
    const data = form.getFieldsValue();
    props.BlogActions.asyncApothegmAddAction({
      author: data.author,
      content: data.content,
    }).then(() => {
      message.success('Ê∑ªÂä†ÊàêÂäü')
      // ÈáçÊñ∞Ë∞ÉÁî®Êü•ËØ¢Êé•Âè£
      props.BlogActions.asyncApothegmListAction(currentPage, pageSize, '').then((res: ApothegmData) => {
        let { data, totalCount, page, pageSize } = res.data as unknown as ApothegmData;
        setList(data);
        setTotal(totalCount);
        setCurrentPage(page);
        setPageSize(pageSize);
      });
    });
    form.resetFields();
    setIsModalOpen(false);
  };
  // ÂÖ≥Èó≠Á™óÂè£
  const handleCancel = () => {
    form.resetFields();
    setIsModalOpen(false);
  };
  // ÂÖ≥Èó≠Á™óÂè£
  const handleUpdateCancel = () => {
    updateForm.resetFields();
    setIsModalUpdateOpen(false);
  };
  // ÁÇπÂáªÊõ¥Êñ∞
  const ApothegmUpdate = (item: ApothegmData) => {
    setIsModalUpdateOpen(true);
    updateForm.setFieldsValue(item);
    setEditData(item);
  };
  // Êõ¥Êñ∞Êìç‰Ωú
  const handleUpdateConfirm = () => {
    if (role_type) {
      return handleNotUpdate()
    }
    let value = updateForm.getFieldsValue();
    props.BlogActions.asyncApothegmUpdateAction({
      author: value.author,
      content: value.content,
      //@ts-ignore
      id: editData._id,
    }).then(() => {
      message.success('Êõ¥Êñ∞ÊàêÂäü');
      props.BlogActions.asyncApothegmListAction(currentPage, pageSize, '').then((res: ApothegmData) => {
        // Ëé∑ÂèñË≠¶Âè•
        let { data, totalCount, page, pageSize } = res.data as unknown as ApothegmData;
        setList(data);
        setTotal(totalCount);
        setCurrentPage(page);
        setPageSize(pageSize);
      });
      updateForm.resetFields();
      setIsModalUpdateOpen(false);
    });
  };
  // Âà†Èô§Ë≠¶Âè•
  const ApothegmDelete = (item: ApothegmData) => {
    confirm({
      title: '‰Ω†Á°ÆÂÆöË¶ÅÂà†Èô§Âêó?',
      icon: <ExclamationCircleOutlined />,
      onOk() {
        if (role_type) {
          return handleNotDelete();
        }
        // ÂÖàÂ∞ÜË¶ÅÂà†Èô§ÁöÑÊï∞ÊçÆËøáÊª§ÊéâÂÜçË∞ÉÁî®Êé•Âè£
        setList(list.filter((it) => it._id !== item._id));
        message.success('Âà†Èô§ÊàêÂäü');
        props.BlogActions.asyncApothegmDeleteAction(item._id).then(() => {
          props.BlogActions.asyncApothegmListAction(currentPage, pageSize, '').then((res: ApothegmData) => {
            // Ëé∑ÂèñË≠¶Âè•
            let { data, totalCount, page, pageSize } = res.data as unknown as ApothegmData;
            setList(data);
            setTotal(totalCount);
            setCurrentPage(page);
            setPageSize(pageSize);
          });
        });
      },
    });
  };
  // Êõ¥Êñ∞Ë≠¶Âè•Áä∂ÊÄÅ
  const onChangeStatus = (checked: boolean, row: ApothegmData) => {
    props.BlogActions.asyncApothegmStatusUpdateAction({
      checked: checked,
      id: row._id,
    }).then((res: { code: number, msg: string }) => {
      if (res.code === 0) {
        row.checked = !row.checked;
        setList([...list]);
        message.success(res.msg);
      }
    });
  };
  // ÊêúÁ¥¢
  const onSearch = (value: string) => {
    props.BlogActions.asyncApothegmListAction(currentPage, pageSize, value).then((res: ApothegmData) => {
      let { data, totalCount, page, pageSize } = res.data as unknown as ApothegmData;
      setList(data);
      setTotal(totalCount);
      setCurrentPage(page);
      setPageSize(pageSize);
    });
  };
  // Ë∑≥ËΩ¨È°µÊï∞ÊçÆÊòæÁ§∫
  const onChangePage = (page: number, pageSize: number, params = '') => {
    // ÈáçÊñ∞Ë∞ÉÁî®Êé•Âè£Â∞ÜÂèÇÊï∞‰º†ÈÄíËøáÂéª
    props.BlogActions.asyncUserListAction(page, pageSize, params).then((res: ApothegmData) => {
      // Ëé∑ÂèñÂàóË°®Êï∞ÊçÆ
      let { data } = res.data as unknown as ApothegmData;
      setList(data);
      // ÂàáÊç¢Ë°å
      setCurrentPage(page);
      // Ê†πÊçÆÈ°µÈù¢Êï∞ÊçÆÊòæÁ§∫È°µÁ†Å
      setPageSize(pageSize);
    });
  };
  return (
    <div>
      <div className="title">
        <Button type="primary" onClick={showModal} className="btn">
          Ê∑ªÂä†ÂêçË®ÄË≠¶Âè•
        </Button>
        <Search
          className="search"
          allowClear
          placeholder="ËØ∑ËæìÂÖ•‰ΩúËÄÖÂêçÁß∞"
          onSearch={onSearch}
          enterButton
        />
      </div>
      <Modal
        open={isModalOpen}
        title={<div style={{ textAlign: 'left' }}>Ê∑ªÂä†ÂêçË®ÄË≠¶Âè•</div>}
        okText="Ê∑ªÂä†"
        cancelText="ÂèñÊ∂à"
        onCancel={handleCancel}
        onOk={() => {
          handleConfirm();
        }}
      >
        <Form form={form} layout="vertical" name="basic" className="userAddFrom">
          <Form.Item name="author" label="‰ΩúËÄÖ" rules={[{ required: true, message: '‰ΩúËÄÖ‰∏çËÉΩ‰∏∫Á©∫' }]}>
            <Input />
          </Form.Item>
          <Form.Item name="content" label="ÂÜÖÂÆπ" rules={[{ required: true, message: 'ÂÜÖÂÆπ‰∏çËÉΩ‰∏∫Á©∫' }]}>
            <TextArea rows={5} />
          </Form.Item>
        </Form>
      </Modal>
      <Modal
        open={isModalUpdateOpen}
        title={<div style={{ textAlign: 'left' }}>Êõ¥Êñ∞ÂêçË®ÄË≠¶Âè•</div>}
        okText="Êõ¥Êñ∞"
        cancelText="ÂèñÊ∂à"
        onCancel={handleUpdateCancel}
        onOk={() => {
          handleUpdateConfirm();
        }}
      >
        <Form form={updateForm} layout="vertical" name="basic" className="userAddFrom">
          <Form.Item name="author" label="‰ΩúËÄÖ" rules={[{ required: true, message: '‰ΩúËÄÖ‰∏çËÉΩ‰∏∫Á©∫' }]}>
            <Input />
          </Form.Item>
          <Form.Item name="content" label="Ë≠¶Âè•ÂÜÖÂÆπ" rules={[{ required: true, message: 'ÂÜÖÂÆπ‰∏çËÉΩ‰∏∫Á©∫' }]}>
            <TextArea rows={5} />
          </Form.Item>
        </Form>
      </Modal>
      <Table
        columns={columns}
        dataSource={list}
        scroll={{ y: 'calc(100vh - 240px)', x: true }}
        rowKey={(item) => {
          return item._id + Date.now();
        }}
        pagination={false}
      />
      <MyPagination
        pageSize={pageSize}
        currentPage={currentPage}
        total={total}
        onChange={onChangePage}
      ></MyPagination>
    </div>
  );
};

const mapDispatchToProps = (dispatch: Dispatch) => {
  return {
    BlogActions: bindActionCreators(BlogActions, dispatch),
  };
};
export default connect(null, mapDispatchToProps)(ApothegmList);
