import { ConfigProvider, Pagination } from 'antd'
import React, { useEffect, useState } from 'react'
import zhCN from 'antd/es/locale/zh_CN';
const MyPagination = (props: any) => {
  // ÂàÜÈ°µÊÄªÊï∞
  const [total, setTotal] = useState(0)
  // ÂΩìÂâçÁ¨¨Âá†È°µ
  const [currentPage, setCurrentPage] = useState(0)
  // ÊØèÈ°µÊòæÁ§∫Êù°Êï∞
  const [pageSize, setPageSize] = useState(0)
  useEffect(() => {
    setTotal(props.total)
    setCurrentPage(props.currentPage)
    setPageSize(props.pageSize)
  }, [props])

  const onChangePage = (page: number, pageSize: number) => {
    // Â∞ÜÂàáÊç¢ÁöÑÈ°µÁ†ÅÂèÇÊï∞‰º†ÈÄíËøáÂéª
    props.onChange(page, pageSize)
  }
  return (
    <div>
      {/* ‰∏≠ÊñáËØ≠Ë®ÄÂåÖ */}
      < ConfigProvider locale={zhCN} >
        <Pagination
          style={{ textAlign: 'center', paddingTop: '10px', borderTop: '2px solid #fafafa' }}
          current={currentPage} // ÂΩìÂâçÈ°µ
          pageSize={pageSize} // ÊØèÈ°µÊù°Êï∞
          total={total} // Êï∞ÊçÆÊÄªÊï∞
          pageSizeOptions={[5, 10, 50, 100]} // ÊØèÈ°µÊòæÁ§∫Â§öÂ∞ëÊù°
          onChange={onChangePage} // ÁõëÂê¨pageSizeÂõûË∞É
          showSizeChanger // Â±ïÁ§∫ pageSize ÂàáÊç¢Âô®
          showQuickJumper // Âø´ÈÄüË∑≥ËΩ¨Ëá≥ÊüêÈ°µ
          showTotal={total => `ÂÖ±Êúâ ${total} Êù°Êï∞ÊçÆ`} // Â±ïÁ§∫È°µÁ†Å
        />
      </ConfigProvider >
    </div>
  )
}
export default MyPagination
